@import "descriptor"
@import "advantages"
@import "services"
@import "application"

.consulting 
    text-align: center
    color: $white
    line-height: 1.4
    padding-bottom: 48px
    position: relative
    z-index: 1
    &::before,
    &::after
        content: ''
        position: absolute
        width: 950px
        height: 1000px
        bottom: -400px
        left: 0
        background-image: url(../img/consulting/bg-left.png)
        background-position: center
        background-repeat: no-repeat
        background-size: contain
        z-index: 4
        pointer-events: none
        @media screen and (max-width: 900px)
            display: none
    &::after
        left: auto
        right: 0
        background-image: url(../img/consulting/bg-right.png)
        z-index: 3
    &__title
        position: relative
        overflow: visible
        padding-top: 96px
        @media screen and (max-width: 900px)
            padding-top: 0
        & h2    
            position: relative
            font-size: 32px
            font-weight: 700
            z-index: 2
        & span
            position: absolute
            z-index: 1
            bottom: 0
            left: 50%
            transform: translateX(-50%)
            color: #142546
            text-shadow: -1px 0 #304564, 0 1px #304564, 1px 0 #304564, 0 -1px #304564
            font-size: 96px
            font-style: normal
            font-weight: 700
            line-height: 1
            @media screen and (max-width: 1104px)
                font-size: 72px
            @media screen and (max-width: 900px)
                display: none
    &__text
        margin-top: 24px
        color: $grey-l