.consulting-application
    margin-top: 96px
    position: relative
    z-index: 4
    @media screen and (max-width: 900px)
        margin-top: 64px
        z-index: 3
        &::before
            content: ''
            position: absolute
            width: 100%
            height: 1000px
            bottom: 0
            right: 0
            background-image: url(../img/consulting/bg-right-mob.png)
            background-position: right center
            background-repeat: no-repeat
            background-size: contain
            z-index: -1
            pointer-events: none
    & .consulting__title
        padding-top: 0
    & .consulting__text 
        margin-top: 16px
    &__inner 
        background-color: #192D4C
        border-radius: 8px
        padding: 48px 32px 32px 32px
        @media screen and (max-width: 900px)
            padding: 32px 24px
    &__form 
        display: flex
        gap: 24px
        text-align: left
        margin-top: 32px
        @media screen and (max-width: 900px)
            flex-direction: column
            gap: 0
    &__field
        flex: 1 0 auto
        position: relative
        @media screen and (max-width: 900px)
            &:not(:first-child)
                margin-top: 16px
    &__input
        width: 100%
        background-color: $dark-grey
        border: 1px solid $dark-grey
        outline: 0
        padding: 18px 16px
        border-radius: 6px
        font-weight: 600
        font-size: 14px
        color: $white
        &::placeholder
            font-weight: 400
            color: #647494
        &:focus-visible
            border: 1px solid #4799CF
        .error & 
            border: 1px solid $red
    &__label 
        margin-bottom: 8px
        font-size: 14px
    &__error
        position: absolute
        top: calc( 100% + 4px )
        font-size: 12px
        color: $red
        display: none
        @media screen and (max-width: 900px)
            top: calc( 100% + 2px )
        .error & 
            display: block
    &__btn
        padding: 17px 93px
        font-weight: 500
        font-size: 14px
        border-radius: 8px
        align-self: flex-end
        @media screen and (max-width: 900px)
            margin-top: 24px
            width: 100%
            padding: 18px
    &__text 
        margin-top: 24px
        font-size: 14px
        color: $grey-l