.consulting-advantages
    margin-top: -104px 
    margin-bottom: 48px
    @media screen and (max-width: 900px)
        margin-top: -60px
        margin-bottom: 64px
    & .container 
        overflow: visible
    &__list
        display: flex
        gap: 24px
        list-style-type: none
        overflow: visible
        @media screen and (max-width: 900px)
            flex-direction: column
            gap: 16px
    &__item 
        border-radius: 8px
        padding: 32px
        width: calc( 33.33333% - 16px )
        background-color: #192D4C
        position: relative
        transition: .3s
        @media screen and (max-width: 900px)
            width: 100%
            padding: 32px 24px 36px
        &::after
            content: ''
            position: absolute
            width: 100%
            height: 32px
            left: 0
            bottom: 0
            background: url(../img/consulting/blur1.png) bottom no-repeat
            background-size: contain
            transition: .3s
        @include hover 
            transform: translateY(-8px)
            &::after
                background: url(../img/consulting/blur2.png) center no-repeat
                background-size: contain
    &__text
        margin-top: 24px
        font-weight: 700
        font-size: 20px