.consulting-descriptor 
    background-image: url('../img/consulting/descriptor-bg.png')
    background-size: cover
    background-position: center
    margin-top: -66px
    padding: 200px 0
    @media screen and (max-width: 1200px)
        margin-top: -81px
    @media screen and (max-width: 900px)
        padding: 182px 0
    &__title 
        font-size: 50px
        @media screen and (max-width: 900px)
            font-size: 28px
    &__btn
        margin-top: 40px
        padding: 17px 32px
        border-radius: 8px
        font-size: 16px
        font-weight: 500
        line-height: 1.4
        display: inline-flex
        align-items: center
        @media screen and (max-width: 900px)
            margin-top: 32px
        & svg 
            width: 16px
            height: 16px
            margin-left: 8px