.consulting-services 
    margin-top: 48px
    overflow: hidden
    @media screen and (max-width: 900px)
        margin-top: 64px
        position: relative
        z-index: 4
        overflow: visible
        &::before
            content: ''
            position: absolute
            width: 100%
            height: 1000px
            top: 15%
            left: 0
            background-image: url(../img/consulting/bg-left-mob.png)
            background-repeat: no-repeat
            background-size: contain
            z-index: -1
            pointer-events: none
    & .container 
        overflow: visible
    &__slider
        margin-top: 32px
        overflow: visible
        position: relative
        z-index: 3
        @media screen and (max-width: 1236px)
            padding: 0 32px
        @media screen and (max-width: 900px)
            padding: 0
        &::before
            position: absolute
            content: ''
            right: calc( 100% + 24px )
            top: -4px
            width: calc((100vw - 1200px) / 2)
            height: calc( 100% + 8px )
            background-color: #142546
            z-index: 2
            @media screen and (max-width: 1236px)
                display: none
        &::after
            position: absolute
            content: ''
            left: calc( 100% + 24px )
            top: -4px
            width: calc((100vw - 1200px) / 2)
            height: calc( 100% + 8px )
            background: linear-gradient(270deg, #142546 62.5%, rgba(20, 37, 70, 0) 100%)
            z-index: 2
            @media screen and (max-width: 1236px)
                display: none
    &__list 
        list-style-type: none
        display: flex
        @media screen and (max-width: 900px)
            flex-direction: column
            gap: 16px
    &__item 
        border: 1px solid #304564
        height: auto
        padding: 32px
        border-radius: 8px
        @media screen and (max-width: 900px)
            display: flex
            align-items: center
            justify-content: center
            gap: 25px
            padding: 26px 24px 24px
            background: #142546
        @media screen and (max-width: 600px)
            justify-content: flex-start
    &__img 
        @media screen and (max-width: 900px)
            flex: 0 0 48px
            height: 48px
            & img 
                max-width: 100%
    &__text 
        margin-top: 24px
        font-weight: 700
        font-size: 20px
        @media screen and (max-width: 900px)
            margin-top: 0
            font-size: 16px
            text-align: left
            & br
                display: none
    &__arrows 
        position: absolute
        width: calc( 100% + 48px )
        height: 48px
        top: calc( 50% - 24px )
        left: -24px
        @media screen and (max-width: 1236px)
            width: calc( 100% + 18px )
            left: -9px
        @media screen and (max-width: 900px)
            display: none
    &__prev,
    &__next
        margin: 0
        width: 48px
        height: 48px
        border-radius: 50%
        background-color: $orange
        background-image: url(../img/consulting/arrow-slider.svg)
        background-position: center
        background-repeat: no-repeat
        right: 0
        outline: 0
        &::after
            display: none
        @include hover
            background-color: $hover-orange
        &.swiper-button-disabled
            opacity: 1
            background-color: #304564
            background-image: url(../img/consulting/arrow-slider-disabled.svg)
    &__prev
        transform: rotate(180deg)
        right: auto
        left: 0